"use client"

import { User } from "@/interface/types";
import { useState } from "react";

type PageProps = {
  params: {
    userId: string;
  };
};

export default function Page({ params}: PageProps) {
  const [selectedUser,setSelectedUser] = useState<User>({} as User);
  

  return <div>User ID: {params.userId}</div>;
}
